<template>
    <div class="recharge-container">
        <!-- 用户充值 -->
        <van-field v-model="value" placeholder="请输入金额"  type="digit" />
        <van-button type="primary" block style="margin-top: 10px;"
            @click="handleClick"
        >提交</van-button>
    </div>
</template>

<script>
import { Notify } from 'vant';
export default {
    data() {
        return {
            value: '',
        };
    },
    methods: {
        handleClick() {
            if(this.value) {
                Notify({ type: 'primary', message: '已充值成功' ,
                duration: 800,});
                this.value = "";
            }
        }
    }
}   
</script>

<style lang="scss" scoped>
    .recharge-container{
        margin-top: 20px;
    }
</style>